a {
	color: #7F2D28;
}
blockquote {
	border-left: 5px solid #7F2D28;
	color: #7F2D28;
	font-size: 125%;
	font-weight: 400;
	margin: 20px 0;
	padding-left: 1.5rem;
}
blockquote * {
  font-size: inherit;
  line-height: inherit;
}

nav {
	background: white;
	-webkit-box-shadow: none;
	        box-shadow: none;
	height: 0;
}
nav i {
	padding: 0 15px;
	position: relative;
	top: 5px;
}
main {
	background: url(../assets/bg.png) repeat;
	padding-left: 190px;
}
ul.side-nav {
	width: 190px;
}
ul.side-nav li {
	padding: 0;
}
ul.side-nav.fixed a {
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
	color: #444;
	display: block;
	line-height: 100%;
	padding: 10px 0 12px;
}
ul.table-of-contents li.logo a.active,
ul.table-of-contents li.logo a:hover {
	border: none;
	font-weight: 200;
}
ul.side-nav.fixed li.logo {
	background-color: #fafafa;
	border-bottom: 1px solid #ddd;
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
	min-height: 90px;
	padding: 30px 30px 40px;
}
ul.side-nav.fixed li.logo a {
	font-weight: 200;
	line-height: 100%;
}
ul.table-of-contents li:not(.logo) a.active,
ul.table-of-contents li:not(.logo) a:hover {
	background-color: #fafafa;
	border-left: 3px solid ;
}
ul.side-nav.fixed.table-of-contents li:not(.logo) a span,
ul#slide-out li:not(.logo) a span {
	color: #333 !important;
}
li.logo span {
	display: block;
	font-size: 14px;
}
.brand-logo h1 {
	font-size: 32px;
	font-weight: 200;
	margin: 0;
	line-height: 32px;
	font-family: Roboto, sans-serif;
}
.container {
	width: 95%
}
h3 {
	font-weight: 200;
	margin-bottom: 60px;
	margin-top: 0;
	padding: 30px 40px;
	text-transform: uppercase;
}

.full-height {
	height: 100vh;
}

.overlay {
	background-color: rgba(0, 0, 0, 0.4);
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.waves-effect.waves-blue .waves-ripple {
   /* The alpha value allows the text and background color
   of the button to still show through. */
    background-color: rgba(33, 150, 243, 0.2);
 }
.card {
	margin-bottom: 60px;
	color: #ffffff;
	background-color: transparent;
	border: 1px solid #ffffff;
}
.card a {
	color: #7F2D28;
}
.card.edu {
	height: 250px;
}
.card .role {
	font-size: 1.25rem;
  position: absolute;
  right: 38px;
  top: 35px;
}
.card li a {
	margin-right: 0;
	color: #333;
}
.card li a:hover {
	color: #333;
	font-weight: 500;
}
.card-text-color {
	color: #ffffff;
}
.card .card-content {
	padding-bottom: 30px;
}
.card .card-content p {
	padding: 5px 0 0;
}
.card h5 {
  font-size: 1.5rem;
  margin-bottom: -0.5rem;
  margin-top: 2rem;
}
.card h6 {
  font-weight: 300;
  letter-spacing: 1px;
  margin-top: 2rem;
  text-transform: uppercase;
}

.card-action span {
	font-size: 100%;
}
.card a:hover {
	color: #7F2D28;
}
.card ul {
	margin-bottom: 0;
}
.card ul li {
	line-height: 200%;
	list-style-type: disc;
	margin-left: 24px;
}
.card ul li li {
  color: #666;
  font-size: 13px;
}
.card .card-title {
	font-size: 24px;
}
.card .card-content .card-title {
	line-height: 2.5rem
}
.card i.right {
  margin-left: 10px;
}
a.hoverline {
	border-bottom: 3px solid transparent !important;
	padding: 3px 3px 0 3px;
	-o-transition: .25s all;
	transition: .25s all;
	-moz-transition: .25s all;
	-webkit-transition: .25s all;
}
a.hoverline:hover {
	border-bottom: 3px solid #009688 !important;
}
#skills .card.large {
	height: 360px;
}
#skills h4 {
	border-bottom: 1px solid #ddd;
	margin-bottom: 30px;
	padding-bottom: 20px;
}
#skills .col img.responsive-img {
	margin: 0 auto;
	max-height: 100px;
	padding-bottom: 10px;
	text-align: center;
	vertical-align: middle;
  display: table-cell;
}
.card.large .card-content p {
	color: inherit;
	margin: 0;
	padding: 30px 0;
}
#experience .card-content .row {
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
}
.card.large .card-image {
	height: 230px;
}
.card.large .card-content {
	height: auto;
}
.card.medium .card-image {
	height: 240px;
}
.card-action a.btn-floating {
	margin-bottom: 0;
}

#contact a:not(.btn-floating) {
	bottom: 6px;
	left: 20px;
	position: relative;
}
#experience .col.s12.m2 {
	text-align: center;
}
.fa-external-link:before {
  left: 3px;
  position: relative;
  top: 2px;
}
small { display: none;}
.grey-text {
	color: #666 !important;
}










